* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input[type=text] {
  border: 3px solid #ccc;
  border-radius: 2px;
  box-sizing: border-box;
  font-size: 1.2rem;
  outline: none;
  padding: 10px 15px;
  margin: 8px 0;
  transition: 0.5s;
  width: 100%;
  -webkit-transition: 0.5s;
}

input[type=text]:focus {
  border: 3px solid rgb(12, 122, 169);
}

.gif-grid {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  justify-content: space-between;
  grid-template-rows: auto;
  grid-column-gap: 30px;
  grid-row-gap: 10px;
}

.card-gif {
  width: 300px;
  border: .5px solid rgba(1, 1, 1, .2);
  border-radius: 6px;
  overflow: hidden;
  padding: 10px;
}

.card-gif .gif-image {
  width: 100%;
  max-height: 170px;
  border-radius: 6px;
  object-fit: cover;
}

.card-gif p {
  text-align: center;
}

@media only screen and (max-width: 790px) {
  .gif-grid {
    justify-content: center;
  }
}